@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

:root {
    --bg-color: #1C1C1C;
    --card-bg-color: #232323;
    --primary-text: #f6f6f6;
    --secondary-text: #e3e7ea;
    --blue: #1B53BA;
    --yellow: #FFCA08;
    --red: #ED1B24;
    --slider-size: 0.75rem;
}

/* Reset browser defaults */
* {
    border: 0;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
}

body {
    width: 100vw;
    min-height: 100vh;
    overflow-x: hidden;
    background-color: var(--bg-color);
    color: var(--primary-text);
    font-family: 'Poppins', sans-serif;
}

main {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url("https://wallpaperaccess.com/full/45704.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: var(--bg-color);
    background-blend-mode: lighten;
}

.login-card {
    width: 22rem;
    min-width: 20rem;
    margin: 5rem;
}

.center-text {
    text-align: center;
}

.card-bg-color {
    background-color: var(--card-bg-color);
}

.card-body {
    padding: 1.5rem;
    padding-bottom: 0;
}

.input-bg {
    background-color: var(--card-bg-color);
    border: 0.1rem gray solid;
    color: var(--primary-text);
}

.input-bg:focus {
    background-color: var(--card-bg-color);
    color: var(--primary-text);
}

.btn-primary {
    background-color: var(--blue);
    border: none;
}

#btn-Google {
    margin-bottom: 2rem;
    background-color: whitesmoke;
    color: var(--bg-color)
}

#google-icon {
    width: 1rem;
    margin-right: 1rem;
}

.secondary {
    color: var(--secondary-text);
    font-size: 0.9rem;
}

a {
    color: var(--primary-text)
}

.container-padding {
    padding: 5rem;
}

/* Header */
.header-container {
    padding: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    display: flex;
    justify-content: space-between;
    background-color: var(--card-bg-color);
}

.nav-home {
    text-decoration: none;
}

.nav-home:hover {
    color: var(--primary-text)
}

.signOut-div:hover {
    cursor: pointer;
}

/* Loading */
.loading-screen {
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loading-screen img {
    height: 4rem;
}

/* Collection */
.add-new-btn {
    background-color: var(--card-bg-color);
    display: block;
    width: 6rem;
    height: 3rem;
    border-radius: 1rem;
}

.add-new-btn:active {
    background-color: var(--bg-color);
}

.my-collections {
    margin-top: 2rem;
    display: flex;
}

/* SelectSet */
.modal-content {
    background-color: var(--card-bg-color);
}

.modal-header, .modal-footer {
    border: none;
}

.modal-header {
    margin-bottom: 2rem;
}

.modal-body {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0rem;
}

.set-card {
    width: 9rem;
    margin: 0.1rem;
    align-items: center;
    justify-content: center;
    padding: 1rem;
}

.set-card img {
    width: 6rem;
}

.set-card:hover {
    cursor: pointer;
    background-color: #3a3a3a;
}

/* Binder */
.binder-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.binder-container h3{
    margin-bottom: 2rem;
}

.bi-trash {
    color: var(--red);
    margin-left: 1rem;
    cursor: pointer;
}

.delete-col-btn {
    background-color: var(--bg-color)
}

.cards-container {
    align-self: center;
    margin-top: 2rem;
    display: grid;
    grid-template-columns: auto auto auto auto;
}

.pkmn-card {
    padding: 1rem;
    padding-bottom: 0.5rem;
    width: 15rem;
    margin: 0.25rem;
}

.pkmn-card img {
    border-radius: 0.25rem;
}

.transparent {
    opacity: 0.3;
}

.switch {
    position: relative;
    display: inline-block;
    width: 2rem;
    height: 1.25rem;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 1rem;
}

.slider:before {
    position: absolute;
    content: "";
    height: var(--slider-size);
    width: var(--slider-size);
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 1rem;
}

input:checked + .slider {
    background-color: var(--blue);
}

input:checked + .slider:before {
    -webkit-transform: translateX(var(--slider-size));
    -ms-transform: translateX(var(--slider-size));
    transform: translateX(var(--slider-size));
}

.pkmn-card-footer {
    margin-top: 0.75rem;
}
